/*preloader component*/
.preloader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  position: relative;
  z-index: 5;
}
.preloader-container::after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #FFF;
  z-index: 1;
  content: "";
}
.preloader-container .loader {
  max-width: 15rem;
  width: 100%;
  height: auto;
  stroke-linecap: round;
  z-index: 2;
}
@keyframes preloader {
  50% {
    transform: rotate(360deg);
  }
}
.preloader-container .loader circle {
  fill: none;
  stroke-width: 3.5;
  animation-name: preloader;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  transform-origin: 170px 170px;
  will-change: transform;
}
.preloader-container .loader circle:nth-of-type(1) {
  stroke-dasharray: 550px;
}
.preloader-container .loader circle:nth-of-type(2) {
  stroke-dasharray: 500px;
}
.preloader-container .loader circle:nth-of-type(3) {
  stroke-dasharray: 450px;
}
.preloader-container .loader circle:nth-of-type(4) {
  stroke-dasharray: 300px;
}
.preloader-container .loader circle:nth-of-type(1) {
  animation-delay: -0.15s;
}
.preloader-container .loader circle:nth-of-type(2) {
  animation-delay: -0.3s;
}
.preloader-container .loader circle:nth-of-type(3) {
  animation-delay: -0.45s;
}
.preloader-container .loader circle:nth-of-type(4) {
  animation-delay: -0.6s;
}

